<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process221"></a>

<p><br></p>
<p><span class="rvts17">原木的复仇（</span><span class="rvts50">The Bark's Bite</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:521,Y:431</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：原木（</span><span class="rvts20">Log</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">帮助原木复仇</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1284.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">在锯木厂中的工作车间，可以找到一块有</span><span class="rvts20"></span><span class="rvts21">灵性</span><span class="rvts20"></span><span class="rvts21">的原木，通过</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">，可以与它交谈。它会告诉你它生前遭遇了孤狼伐木工的残忍对待。</span>
<br><span class="rvts21">找到伐木工，并杀掉他，为原木复仇。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1285.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">伐木工其实也死掉了，在锯木厂大门外围的陷阱区域（位置</span><span class="rvts20">X:479,Y:374</span><span class="rvts21">），可以找到伐木工的墓碑，使用源力视野，尝试与它的灵魂交谈，并吸取它的灵魂。</span>
<br><span class="rvts21">再次返回锯木厂找到原木对话后，可成功关闭该支线并获得额外经验。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



